.premium-form-radio {
    .premium-form-radio-item {
        margin-bottom: 10px;
        .premium-radio-item {
            input:first-child {
                margin-right: 10px;
            }
        }
        .premium-radio-item-input-wrap {
            display: block;
            position: relative;
            margin-right: 12px;
            cursor: pointer;
            user-select: none;
            input {
                position: absolute;
                opacity: 0;
                cursor: pointer;
                height: 100%;
                width: 100%;
                z-index: 9;
                margin: 0 !important;
                &:checked ~ .premium-radio-item-input-checkmark:after {
                    display: block;
                }
                &::before,
                &::after {
                    content: "";
                    display: none;
                }
            }
            .premium-radio-item-input-checkmark {
                position: relative;
                display: flex;
                align-items: center;
                justify-content: center !important;
                position: relative;
                height: 20px;
                width: 20px;
                border: 1px solid #bdbdbd;
                border-radius: 50%;
                &:after {
                    content: "";
                    display: none;
                    width: 5px;
                    height: 5px;
                    border-radius: 50%;
                    background-color: #2196f3;
                }
            }
        }
    }
}
