.premium-form-toggle {
    .premium-form-toggle-field {
        margin-bottom: 20px;
        display: flex;
        align-items: center;
        .premium-form-toggle-label {
            margin-left: 10px;
        }
        .premium-form-switch {
            position: relative;
            display: inline-block;
            width: 45px;
            height: 20px;
            input {
                opacity: 0;
                width: 0;
                height: 0;
                &:checked + .premium-form-slider {
                    background-color: #2196f3;
                }

                &:focus + .premium-form-slider {
                    box-shadow: 0 0 1px #2196f3;
                }

                &:checked + .premium-form-slider:before {
                    transform: translateX(22px);
                }
            }
            .premium-form-slider {
                position: absolute;
                cursor: pointer;
                top: 0;
                left: 0;
                right: 0;
                bottom: 0;
                background-color: #ccc;
                transition: 0.4s;
                &:before {
                    position: absolute;
                    content: "";
                    height: 15px;
                    width: 15px;
                    left: 4px;
                    bottom: 3px;
                    background-color: #fff;
                    transition: 0.4s;
                }
                &.rounded {
                    border-radius: 34px;
                    &:before {
                        border-radius: 50%;
                    }
                }
            }
        }
    }
}
