.premium-form-select {
    select {
        width: 100%;
        height: 40px;
        border: 1px solid var(--pbg-global-color4, #e1e1e1);
        border-radius: 4px;
        padding: 0 15px;
        font-size: 14px;
        transition: all 0.3s ease-in-out;
        &:focus {
            outline: none;
            box-shadow: none;
        }
    }
}
