.premium-form {
    width: 100%;
    .premium-button {
        display: inline-flex;
        position: relative;
        overflow: hidden;
        cursor: pointer;
        transition: all 0.2s ease-in-out !important;
        text-decoration: none;
        white-space: normal;
        word-break: break-all;
        justify-content: center;
        align-items: center;
        border-radius: 0%;
    }
    .wp-block-button__link.premium-form-submit {
        background-color: var(--pbg-global-color1, #0085ba);
        color: #fff;
        &.loading {
            // Add disabled styles
            opacity: 0.5;
            pointer-events: none;
            cursor: not-allowed;
            align-items: center;
            &::after {
                content: "\f463";
                font-family: "dashicons";
                font-size: 20px;
                animation: lds-ring 1.2s linear infinite;
                display: inline-block;
                vertical-align: middle;
                margin-left: 10px;
            }
        }
        @keyframes lds-ring {
            0% {
                transform: rotate(0deg);
            }
            100% {
                transform: rotate(360deg);
            }
        }
    }

    .premium-form-field-wrap {
        margin-bottom: 20px;
    }

    .premium-form-input {
        padding: 7px 10px;
        width: 100%;
        &:focus {
            outline: none;
        }
    }

    .premium-form-input__icon-wrap + .premium-form-input {
        padding-left: 40px;
    }

    .premium-form-input-label:not(.premium-form-toggle-label):not(
            .premium-form-accept-text
        ) {
        display: inline-block;
        margin-bottom: 5px;
    }
    input[type="email"],
    input[type="text"],
    input[type="url"],
    input[type="tel"],
    input[type="date"],
    textarea {
        border: 1px solid var(--pbg-global-color4, #e1e1e1);
        border-radius: 0px;
        box-shadow: none !important;
        color: #50575e;
        min-height: 30px;
        height: auto;
        line-height: normal;
        font-size: 14px;
        font-family: inherit;
        &:focus {
            outline: none;
            border-color: var(--pbg-global-color4, #e1e1e1);
        }
    }
    .premium-form-input-wrap {
        display: flex;
        align-items: center;
        flex-wrap: wrap;
        position: relative;
        .premium-form-input__icon-wrap {
            position: absolute;
            left: 11px;
            top: 50%;
            transform: translateY(-50%);
            z-index: 1;
            .premium-form-input__content-icon {
                display: flex;
            }
        }
    }
    .premium-form-hidden-save {
        display: none !important;
    }
    .premium-form-field-required {
        .premium-form-input-label::after {
            color: #f00;
            content: " *";
        }
    }
    ul.premium-form-errors {
        margin: 0;
        margin-top: 20px;
        padding: 0;
        list-style: none;
        font-size: 14px;
        font-weight: 400;
        padding: 10px;
        border: 1px solid #f00;
        border-radius: 2px;
        background: #fff5f5;
        color: #c53030;
        li {
            margin-bottom: 10px;
        }
    }

    .premium-form-success-message {
        background: #d4edda;
        border: 1px solid #c3e6cb;
        border-radius: 2px;
        color: #155724;
        font-weight: 600;
        width: 100%;
        display: inline-block;
        padding: 10px;
        margin-top: 20px;
    }

    .premium-form-error-message {
        background: #fff5f5;
        border: 1px solid #fc8181;
        border-radius: 2px;
        color: #c53030;
        font-weight: 600;
        width: 100%;
        display: inline-block;
        padding: 10px;
        margin-top: 20px;
    }

    .premium-form-recaptcha {
        margin-bottom: 20px;
    }

    .premium-form-input-error {
        border: 1px solid #c53030 !important;
    }

    .premium-form-input-error-message {
        background: #fff5f5;
        border: 1px solid #fc8181;
        border-radius: 2px;
        color: #c53030;
        font-weight: 700;
        width: 100%;
        display: inline-block;
        padding: 10px;
    }
    .wp-block-button__link {
        width: auto;
        &.premium-button__sm {
            padding: 12px 24px;
            font-size: 14px;
            line-height: 1;
            box-sizing: border-box;
        }

        &.premium-button__md {
            padding: 14px 26px;
            font-size: 16px;
            line-height: 1.2;
            box-sizing: border-box;
        }

        &.premium-button__lg {
            padding: 16px 28px;
            font-size: 18px;
            line-height: 1.3333;
            box-sizing: border-box;
        }
        &.premium-button__block {
            width: 100%;
            text-align: center;
            flex: 1 0 fit-content;
        }
    }
    .premium-form-submit-wrap {
        display: flex;
    }
}

/************ Responsive **************/
@media (min-width: 977px) {
    .premium-desktop-hidden {
        display: none !important;
    }
}

@media only screen and (max-width: 976px) and (min-width: 766px) {
    .premium-tablet-hidden {
        display: none !important;
    }
}

@media (max-width: 767px) {
    .premium-mobile-hidden {
        display: none !important;
    }
}
