.premium-form-checkbox {
    .premium-form-checkbox-item {
        margin-bottom: 10px;
        .premium-checkbox-item {
            input:first-child {
                margin-right: 10px;
            }
        }
        .premium-checkbox-item-input-wrap {
            display: block;
            position: relative;
            margin-right: 12px;
            cursor: pointer;
            user-select: none;
            input:checked ~ .premium-checkbox-item-input-checkmark {
                background-color: #2196f3;
            }
            input {
                position: absolute;
                opacity: 0;
                cursor: pointer;
                height: 100%;
                width: 100%;
                z-index: 9;
                margin: 0 !important;
                &:checked ~ .premium-checkbox-item-input-checkmark:after {
                    display: block;
                }
                &::before,
                &::after {
                    content: "";
                    display: none;
                }
            }
            .premium-checkbox-item-input-checkmark {
                position: relative;
                display: flex;
                align-items: center;
                justify-content: center !important;
                position: relative;
                height: 20px;
                width: 20px;
                border: 1px solid #bdbdbd;
                border-radius: 5px;
                &:after {
                    content: "";
                    display: none;
                    width: 5px;
                    height: 11px;
                    border: solid #fff;
                    border-width: 0 2px 2px 0;
                    -webkit-transform: rotate(40deg);
                    -ms-transform: rotate(40deg);
                    transform: rotate(40deg);
                }
            }
        }
    }
}
