.premium-form-phone {
    .premium-form-phone-input-wrap {
        display: flex;
        select {
            margin: 0px;
            margin-right: 10px;
            color: #50575e;
            text-transform: capitalize;
            border: 1px solid var(--pbg-global-color4, #e1e1e1);
            min-height: 40px;
            padding: 0px 26px 0px 8px;
            overflow: hidden;
            box-shadow: none !important;
            border-radius: 0px;
            width: 30%;
            box-sizing: border-box;
            background-color: transparent;
            &:focus {
                outline: none;
                border: 1px solid var(--pbg-global-color4, #e1e1e1);
            }
        }
        input[type="tel"] {
            min-height: 40px;
            padding: 0px 8px;
            border: 1px solid var(--pbg-global-color4, #e1e1e1);
            border-radius: 0px;
            box-shadow: none !important;
            color: #50575e;
            width: 70%;
            box-sizing: border-box;
            &:focus {
                outline: none;
                border: 1px solid var(--pbg-global-color4, #e1e1e1);
            }
        }
    }
}
